/* // date picker */

.react-datepicker__input-container input {

  display: block;

  width: 100%;

  border-radius: 0.25rem;

  border-width: 1px;

  --tw-border-opacity: 1;

  border-color: hsl(var(--muted) / var(--tw-border-opacity));

  --tw-bg-opacity: 1;

  background-color: rgb(255 255 255 / var(--tw-bg-opacity));

  font-size: 1rem;

  line-height: 1.5rem;

  --tw-shadow: 0 1px 2px 0 rgb(0 0 0 / 0.05);

  --tw-shadow-colored: 0 1px 2px 0 var(--tw-shadow-color);

  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow)
}

@media (min-width: 768px) {

  .react-datepicker__input-container input {

    font-size: 0.875rem;

    line-height: 1.25rem
  }
}

.react-datepicker-popper {

  z-index: 40;

  width: 18rem;

  border-radius: 0.25rem;

  border-width: 2px;

  --tw-border-opacity: 1;

  border-color: hsl(var(--background) / var(--tw-border-opacity));

  background-color: hsl(var(--muted) / 0.2);

  padding-left: 0.75rem;

  padding-right: 0.75rem;

  padding-top: 0.5rem;

  padding-bottom: 0.5rem;

  font-size: 0.875rem;

  line-height: 1.25rem;

  --tw-shadow: 0 1px 3px 0 rgb(0 0 0 / 0.1), 0 1px 2px -1px rgb(0 0 0 / 0.1);

  --tw-shadow-colored: 0 1px 3px 0 var(--tw-shadow-color), 0 1px 2px -1px var(--tw-shadow-color);

  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow)
}

.react-datepicker-left {

  position: absolute !important;

  left: 0px !important;

  right: auto !important;

  top: 2.75rem !important;

  transform: none !important
}

.react-datepicker-right {

  position: absolute !important;

  left: auto !important;

  right: 0px !important;

  top: 2.75rem !important;

  transform: none !important
}

.react-datepicker__portal {

  position: absolute;

  right: 0px;

  top: 3rem;

  z-index: 10;

  width: 18rem;

  transform: none;

  border-radius: 0.25rem;

  border-width: 2px;

  --tw-border-opacity: 1;

  border-color: hsl(var(--background) / var(--tw-border-opacity));

  --tw-bg-opacity: 1;

  background-color: rgb(255 255 255 / var(--tw-bg-opacity));

  padding-left: 0.75rem;

  padding-right: 0.75rem;

  padding-top: 0.5rem;

  padding-bottom: 0.5rem;

  font-size: 0.875rem;

  line-height: 1.25rem;

  --tw-shadow: 0 1px 3px 0 rgb(0 0 0 / 0.1), 0 1px 2px -1px rgb(0 0 0 / 0.1);

  --tw-shadow-colored: 0 1px 3px 0 var(--tw-shadow-color), 0 1px 2px -1px var(--tw-shadow-color);

  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow)
}

.react-datepicker__month-container {

  display: flex;

  flex-direction: column
}

.react-datepicker__month {

  display: flex;

  flex-direction: column
}

.react-datepicker__month > :not([hidden]) ~ :not([hidden]) {

  --tw-space-y-reverse: 0;

  margin-top: calc(-1px * calc(1 - var(--tw-space-y-reverse)));

  margin-bottom: calc(-1px * var(--tw-space-y-reverse))
}

.react-datepicker__current-month {

  margin-left: 0.625rem;

  font-size: 1.125rem;

  line-height: 1.75rem;

  font-weight: 600;

  --tw-text-opacity: 1;

  color: hsl(var(--border) / var(--tw-text-opacity))
}

.react-datepicker__week {

  display: flex
}

.react-datepicker__week > :not([hidden]) ~ :not([hidden]) {

  --tw-space-x-reverse: 0;

  margin-right: calc(-1px * var(--tw-space-x-reverse));

  margin-left: calc(-1px * calc(1 - var(--tw-space-x-reverse)))
}

.react-datepicker__day-names {

  display: flex
}

.react-datepicker__day-names > :not([hidden]) ~ :not([hidden]) {

  --tw-space-x-reverse: 0;

  margin-right: calc(-1px * var(--tw-space-x-reverse));

  margin-left: calc(-1px * calc(1 - var(--tw-space-x-reverse)))
}

.react-datepicker__day-names {

  text-align: center;

  font-size: 0.75rem;

  line-height: 1rem;

  font-weight: 400;

  --tw-text-opacity: 1;

  color: hsl(var(--muted-foreground) / var(--tw-text-opacity))
}

.react-datepicker__day-name {

  display: flex;

  height: 2.5rem;

  width: 2.5rem;

  align-items: center;

  justify-content: center;

  border-radius: 9999px;

  padding-top: 0.25rem;

  padding-bottom: 0.25rem
}

.react-datepicker__navigation {

  position: absolute;

  top: 0.5rem
}

.react-datepicker__navigation--previous {

  right: 3rem;

  display: flex;

  height: 2.5rem;

  width: 2.5rem;

  align-items: center;

  justify-content: center;

  border-radius: 0.25rem;

  transition-property: color, background-color, border-color, text-decoration-color, fill, stroke, opacity, box-shadow, transform, filter, -webkit-backdrop-filter;

  transition-property: color, background-color, border-color, text-decoration-color, fill, stroke, opacity, box-shadow, transform, filter, backdrop-filter;

  transition-property: color, background-color, border-color, text-decoration-color, fill, stroke, opacity, box-shadow, transform, filter, backdrop-filter, -webkit-backdrop-filter;

  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

  transition-duration: 150ms
}

.react-datepicker__navigation--previous:hover {

  --tw-bg-opacity: 1;

  background-color: hsl(var(--background) / var(--tw-bg-opacity))
}

.react-datepicker__navigation--next {

  right: 1rem;

  display: flex;

  height: 2.5rem;

  width: 2.5rem;

  align-items: center;

  justify-content: center;

  border-radius: 0.25rem;

  transition-property: color, background-color, border-color, text-decoration-color, fill, stroke, opacity, box-shadow, transform, filter, -webkit-backdrop-filter;

  transition-property: color, background-color, border-color, text-decoration-color, fill, stroke, opacity, box-shadow, transform, filter, backdrop-filter;

  transition-property: color, background-color, border-color, text-decoration-color, fill, stroke, opacity, box-shadow, transform, filter, backdrop-filter, -webkit-backdrop-filter;

  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

  transition-duration: 150ms
}

.react-datepicker__navigation--next:hover {

  --tw-bg-opacity: 1;

  background-color: hsl(var(--background) / var(--tw-bg-opacity))
}

.react-datepicker__day {

  display: flex;

  height: 2.5rem;

  width: 2.5rem;

  align-items: center;

  justify-content: center;

  font-size: 0.875rem;

  line-height: 1.25rem;

  font-weight: 400;

  line-height: 2;

  --tw-text-opacity: 1;

  color: hsl(var(--muted-foreground) / var(--tw-text-opacity));

  transition-property: color, background-color, border-color, text-decoration-color, fill, stroke, opacity, box-shadow, transform, filter, -webkit-backdrop-filter;

  transition-property: color, background-color, border-color, text-decoration-color, fill, stroke, opacity, box-shadow, transform, filter, backdrop-filter;

  transition-property: color, background-color, border-color, text-decoration-color, fill, stroke, opacity, box-shadow, transform, filter, backdrop-filter, -webkit-backdrop-filter;

  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

  transition-duration: 150ms
  /* @apply mb-1 py-1; */
}

.react-datepicker__day > :not([hidden]) ~ :not([hidden]) {

  --tw-space-y-reverse: 0;

  margin-top: calc(-1px * calc(1 - var(--tw-space-y-reverse)));

  margin-bottom: calc(-1px * var(--tw-space-y-reverse))
}

.react-datepicker__day {

  border-width: 1px
}

.react-datepicker__day--disabled {

  cursor: not-allowed;

  opacity: 0.4
}

.react-datepicker__day--disabled:hover {

  background-color: transparent
}

.react-datepicker__day--outside-month {

  --tw-text-opacity: 1;

  color: hsl(var(--border) / var(--tw-text-opacity))
}

.react-datepicker__day--in-range {

  border-color: hsl(var(--primary)/ 0.1);

  background-color: hsl(var(--primary)/ 0.3);

  letter-spacing: -0.025em
}

.react-datepicker__day--in-selecting-range {

  border-color: hsl(var(--primary)/ 0.1);

  background-color: hsl(var(--primary)/ 0.2);

  letter-spacing: -0.025em
}

.react-datepicker__day--selecting-range-start {

  --tw-bg-opacity: 1;

  background-color: hsl(var(--background) / var(--tw-bg-opacity))
}

.react-datepicker__day--selecting-range-end {

  --tw-bg-opacity: 1;

  background-color: hsl(var(--background) / var(--tw-bg-opacity))
}

.react-datepicker__day--selected,
.react-datepicker__day--selected.react-datepicker__day--highlighted {

  background-color: hsl(var(--primary)/ 0.8);

  --tw-text-opacity: 1;

  color: rgb(255 255 255 / var(--tw-text-opacity))
}

.react-datepicker__day--range-start {

  --tw-bg-opacity: 1;

  background-color: hsl(var(--primary)/ var(--tw-bg-opacity));

  --tw-text-opacity: 1;

  color: rgb(255 255 255 / var(--tw-text-opacity))
}

.react-datepicker__day--range-start:hover {

  background-color: hsl(var(--primary)/ 0.8);

  --tw-text-opacity: 1;

  color: hsl(var(--border) / var(--tw-text-opacity))
}

.react-datepicker__day--range-end {

  --tw-bg-opacity: 1;

  background-color: hsl(var(--primary)/ var(--tw-bg-opacity));

  --tw-text-opacity: 1;

  color: rgb(255 255 255 / var(--tw-text-opacity))
}

.react-datepicker__day--range-end:hover {

  background-color: hsl(var(--primary)/ 0.8);

  --tw-text-opacity: 1;

  color: hsl(var(--border) / var(--tw-text-opacity))
}

.react-datepicker__day--highlighted {

  background-color: hsl(var(--primary)/ 0.6);

  --tw-text-opacity: 1;

  color: hsl(var(--muted-foreground) / var(--tw-text-opacity))
}
